
@font-face {
    font-family: "TT Lakes Regular";
    src        : url(../fonts/TTLakes-Regular.woff) format("woff");
}

@font-face {
    font-family: "TT Lakes Bold";
    src        : url(../fonts/TTLakes-Bold.woff) format("woff");
}

@font-face {
    font-family: "TT Lakes Medium";
    src        : url(../fonts/TTLakes-Medium.woff) format("woff");
}

html {
    box-sizing: border-box;
}

* {
    margin : 0;
    padding: 0;
}

body {
    margin          : 0 auto;
    padding         : 0;
    font-family     : "TT Lakes Regular";
    background-color: #f2f2f2;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
}

.display-none {
    display: none;
}

.restrictScroll {
    overflow: hidden;
}

.not-visible {
    visibility: hidden;
    opacity   : 0;
}

.visible {
    visibility: visible;
    opacity   : 1;
}

.page {
    width  : 100%;
    display: flex;
    margin : 0 auto;
}

.nav__item a:link,
.nav__item a:visited {
    color: #7e7e82;
}

.page__general {
    margin-right    : auto;
    padding         : 42px 0 0 0;
    max-width       : 1120px;
    background-color: #f8f8f8;

    @media screen and (max-width: 768px) and (min-width: 321px) {
        padding-top: 0;
        max-width  : 768px;
    }

    @media screen and (max-width: 320px) {
        padding-top: 0;
        max-width  : 320px;
    }
}

.container,
.main__header {
    margin: 0 auto;
    width : 90%;

    @media screen and (max-width: 768px) and (min-width: 321px) {
        margin: 0 auto;
        width : 95%;
    }
}

.container--display {
    @media screen and (max-width: 320px) {
        display: none;
    }
}

.display {
    &--block {
        display: block;
    }

    &--flex {
        display: flex;
    }
}

.button {
    border    : none;
    outline   : none;
    cursor    : pointer;
    background: none;

    &__name {
        font-size  : 1rem;
        margin-left: 1rem;
    }

    &--call {
        background-image: url("../img/call.svg");
    }

    &--chat {
        background-image: url("../img/chat.svg");
    }

    &--profile {
        background-image: url("../img/profile.svg");
    }

    &--burger-close {
        background-image: url("../img/burger-close.svg");
        display         : none;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            display: block;
        }

        @media screen and (max-width: 320px) {
            display: block;
        }
    }

    &--search {
        background-image: url("../img/search.svg");
    }
}

.header {
    display         : none;
    background-color: #ffffff;
    border-bottom   : 1px solid #d9fff5;

    @media screen and (max-width: 768px) and (min-width: 321px) {
        display: block;
    }

    @media screen and (max-width: 320px) {
        display: block;

        &__left .header__hr {
            display: none;
        }

        &__status .header__hr {
            margin-right : 1rem;
            display      : block;
            width        : 2px;
            height       : 32px;
            background   : #eaeaea;
            opacity      : 0.5;
            border-radius: 1px;
        }
    }

    &--margin {
        margin-bottom: 24px;
    }

    &__logo {
        transition-delay   : 50ms;
        transition-duration: 300ms;
        transition-property: opacity;

        &:hover {
            opacity: 0.6;
        }
    }

    &__block {
        @media screen and (max-width: 768px) and (min-width: 321px) {
            display: block;
        }
    }

    &__container {
        height         : 88px;
        display        : flex;
        flex-direction : row;
        justify-content: space-between;
        align-items    : center;
    }

    &__left,
    &__right,
    &__support,
    &__status {
        display        : flex;
        justify-content: space-between;
        align-items    : center;
    }

    &__button--call,
    &__button--chat,
    &__button--profile,
    &__logo,
    &__status .header__button--repair {
        margin-right: 1rem;
    }

    &__left div:first-child {
        margin-right: 1rem;
        height      : 40px;
    }

    &__left div:last-child {
        padding-top: 5px;
    }

    &__button--call,
    &__button--chat,
    &__button--profile {
        display: none;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            display: block;
        }
    }

    &__button {
        min-width    : 40px;
        min-height   : 40px;
        border-radius: 50%;
        background   : #ff3e79;
    }

    &__button:hover {
        transform                 : scale(1.03);
        opacity                   : 0.6;
        transition-duration       : 0.3s;
        transition-delay          : 150ms;
        transition-timing-function: cubic-bezier(1, 0, 0, 1);
    }

    &__button--repair {
        background-image: url("../img/repair.svg");
    }

    &__button--checkstatus {
        background-image: url("../img/checkstatus.svg");
    }

    &__button--burger {
        background-image: url("../img/burger.svg");
    }

    &__button--call {
        background-image: url("../img/call.svg");
    }

    &__button--chat {
        background-image: url("../img/chat.svg");
    }

    &__button--profile {
        background-image: url("../img/profile.svg");
    }

    &__hr {
        @media screen and (max-width: 768px) and (min-width: 321px) {
            margin-right : 1rem;
            display      : block;
            width        : 2px;
            height       : 32px;
            background   : #eaeaea;
            opacity      : 0.5;
            border-radius: 1px;
        }
    }

    &__hr--none {
        display: none;
    }
}

.main {
    &__container {
        display        : flex;
        position       : relative;
        justify-content: space-between;
        margin         : 0 0 20px 0;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            position: static;
        }

        @media screen and (max-width: 320px) {
            position: static;
        }
    }

    &__title {
        font-family: "TT Lakes Bold";
        font-size  : 1.8rem;

        &::before {
            content         : "";
            position        : absolute;
            left            : -5.5%;
            width           : 4px;
            height          : 40px;
            background-color: #41f6d7;
            border-radius   : 0 5px 5px 0;

            @media screen and (max-width: 768px) and (min-width: 321px) {
                left: 0;
            }

            @media screen and (max-width: 320px) {
                left: 0;
            }
        }
    }

    &__buttons {
        display       : flex;
        flex-direction: row;
        align-items   : center;
        font-family   : "TT Lakes Medium";

        @media screen and (max-width: 768px) and (min-width: 321px) {
            display: none;
        }

        @media screen and (max-width: 320px) {
            display: none;
        }
    }

    &__buttons p {
        margin-right: 8px;
    }

    &__buttons img {
        width: 40px;
    }

    &__button {
        display    : flex;
        align-items: center;
        font-size  : 1rem;
        font-family: "TT Lakes Medium";
        background : none;

        &:hover {
            transform                 : scale(1.01);
            opacity                   : 0.6;
            transition-duration       : 0.3s;
            transition-delay          : 150ms;
            transition-timing-function: cubic-bezier(1, 0, 0, 1);
        }
    }

    &__button--repair {
        margin-right: 2rem;
    }

    &__button--repair::after {
        content: url(../img/repair.svg);
    }

    &__button--checkstatus::after {
        content: url(../img/checkstatus.svg);
    }
}

.nav {
    &--flex {
        margin-bottom : 24px;
        display       : flex;
        flex-direction: row;
        flex-wrap     : wrap;

        overflow          : auto;
        -ms-overflow-style: none;
        scrollbar-width   : none;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            flex-wrap: nowrap;
        }

        @media screen and (max-width: 320px) {
            flex-wrap: nowrap;
        }
    }

    &--flex::-webkit-scrollbar {
        width : 0;
        height: 0;
    }

    &::after {
        content         : "";
        position        : absolute;
        right           : 0;
        width           : 60px;
        height          : 30px;
        background-color: #f2f2f2;
        filter          : blur(10px);
    }

    &__item {
        padding    : 8px 16px;
        color      : #7e7e82;
        white-space: nowrap;
        font-family: "TT Lakes Medium";
    }

    &__item:hover {
        border          : 2px solid #b8ffec;
        border-radius   : 6px;
        background-color: #ffffff;
        padding         : 6px 14px;
    }

}

.content {
    &--flex {
        max-width     : 1120px;
        width         : 90%;
        margin        : 0 auto 30px auto;
        display       : flex;
        flex-direction: row;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            max-width     : 90%;
            margin        : 0 auto;
            display       : flex;
            flex-direction: row;
        }

        @media screen and (max-width: 320px) {
            width  : 100%;
            display: block;
        }
    }

    &__text {
        margin-bottom: 16px;
        padding-right: 2rem;
        font-size    : 0.875rem;
    }

    &__320,
    &__768 {
        display: inline;

        @media screen and (max-width: 320px) {
            display: none;
        }
    }

    &__768 {
        @media screen and (max-width: 768px) and (min-width: 321px) {
            display: none;
        }
    }

    &__text--hidden {
        display: none;
    }

    &__button {
        margin: 0 0 24px 8px;
    }

    &__button:hover {
        transform                 : scale(1.1);
        transition-duration       : 0.3s;
        transition-delay          : 150ms;
        transition-timing-function: cubic-bezier(1, 0, 0, 1);
    }

    &__img img {
        width: 100%;
    }

    &__service .service__container:nth-child(2) {
        transform: translateY(-40px);
    }
}

.service {
    &__container {
        margin-bottom: 50px;

        &:nth-child(2) {
            @media screen and (max-width: 320px) {
                margin-bottom: 0;
            }

        }

        &:last-child {
            @media screen and (max-width: 320px) {
                margin-top: 0;
                transform : translateY(-30px);
            }

        }

        &:last-child .swiper-container {
            @media screen and (max-width: 320px) {
                height: 270px;
            }

        }
    }

    &__title {
        text-transform: uppercase;
        color         : #7e7e82;
    }

    &__header {
        min-height      : 56px;
        display         : flex;
        align-items     : center;
        border-top      : 1px solid #d9fff5;
        background-image: linear-gradient(to right, #f8f8f8 20%, #ffffff 70%);
    }

    &__button:hover {
        transform                 : scale(1.1);
        transition-duration       : 0.3s;
        transition-delay          : 150ms;
        transition-timing-function: cubic-bezier(1, 0, 0, 1);
    }

    &__brands {
        padding        : 24px 0;
        display        : flex;
        justify-content: space-between;
        flex-wrap      : wrap;
    }

    &__equipment {
        padding-bottom : 24px;
        display        : flex;
        flex-wrap      : wrap;
        justify-content: space-between;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            justify-content: flex-start;
        }
    }
}

.brands {
    &__button {
        width        : 240px;
        height       : 72px;
        margin-bottom: 1rem;
        padding      : 0 1rem;

        display        : flex;
        align-items    : center;
        justify-content: space-between;

        border       : 1px solid #eaeaea;
        border-radius: 6px;
        background   : #ffffff;

        @media screen and (max-width: 320px) {
            width: 200px;
        }

        &::after {
            content: url(../img/go.svg);
        }

        &:hover::after {
            content                   : url(../img/go.svg);
            transform                 : scale(1.05) translateX(-5px);
            transition-duration       : 1s;
            transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
        }

    }

    &__button:hover img {
        transform                 : translateX(5px);
        transition-duration       : 1s;
        transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    &__display--none {
        display: none;
    }

    &__swiper::after {
        @media screen and (max-width: 320px) {
            content         : "";
            position        : absolute;
            right           : 0;
            top             : 20px;
            width           : 30px;
            height          : 100px;
            background-color: #f8f8f8;
            filter          : blur(10px);
            z-index         : 2;
        }
    }

    &__img {
        margin-left: 10px;
    }
}

.equipment {
    &__button {
        position      : relative;
        width         : 240px;
        height        : 160px;
        margin-top    : 1rem;
        padding       : 16px;
        text-align    : start;
        vertical-align: top;

        background-color: #ffffff;
        border          : 1px solid #eaeaea;
        border-radius   : 6px;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            margin-right: 3px;
        }

        @media screen and (max-width: 320px) {
            width : 200px;
            height: 140px;
        }

        &::after {
            content : url(../img/go.svg);
            position: absolute;
            right   : 17px;
            bottom  : 14px;
        }

        &:hover::after {
            content                   : url(../img/go.svg);
            transform                 : scale(1.1) translateX(-8px);
            transition-duration       : 1s;
            transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
        }
    }

    &__button span {
        position   : relative;
        top        : -58px;
        font-family: "TT Lakes Medium";
        font-size  : 1rem;
    }

    &__button:hover span {
        display                   : block;
        transform                 : translateX(5px);
        transition-duration       : 1s;
        transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    &__display--none {
        display   : none;
        margin-top: 1rem;
    }

    &__swiper-container {
        @media screen and (max-width: 320px) {
            height: 245px;
        }

        &::after {
            @media screen and (max-width: 320px) {
                content         : "";
                position        : absolute;
                right           : 0;
                top             : 20px;
                width           : 50px;
                height          : 200px;
                background-color: #f8f8f8;
                filter          : blur(8px);
                z-index         : 2;
                transform       : translateX(25px);
            }
        }
    }

    &__swiper-wrapper {
        @media screen and (max-width: 320px) {
            margin-top: -15px;
        }
    }

    &__img {
        margin-left: 10px;
    }
}

.swiper {
    &-container {
        display: none;

        @media screen and (max-width: 320px) {
            display: block;
            width  : 320px;
            height : 140px;
        }
    }

    @media screen and (max-width: 320px) {
        &-slide {
            text-align: center;
            background: #f8f8f8;

            display                : -webkit-box;
            display                : -ms-flexbox;
            display                : -webkit-flex;
            display                : flex;
            -webkit-box-pack       : center;
            -ms-flex-pack          : center;
            -webkit-justify-content: center;
            justify-content        : center;
            -webkit-box-align      : center;
            -ms-flex-align         : center;
            -webkit-align-items    : center;
            align-items            : center;
        }

        &-pagination-bullet {

            background-color: #dddddd !important;
            border          : 5px solid #cccccc;
        }

        &-pagination-bullet-active {
            background-color: #b5b6bc !important;
        }

        &__content {
            display         : flex;
            flex-direction  : column;
            justify-content : flex-start;
            margin-top      : 10px;
            padding         : 0.9rem 0.9rem 0 0.9rem;
            width           : 225px;
            background-color: #ffffff;
            border-radius   : 6px;

            position: relative;
            top     : -20px;
        }

        &__main {
            display        : flex;
            flex-direction : row;
            justify-content: space-between;
        }

        &__title {
            color    : #7E7E82;
            font-size: 0.875rem;
        }

        &__text,
        &__header {
            text-align: start;
        }

        &__header p,
        &__text p {
            font-size    : 0.875rem;
            margin-bottom: 1rem;
        }

        &__button {
            padding-top: 60px;
        }

        &__pagination {
            top: 225px !important;
        }
    }

}

.price {
    &__table {
        width          : 100%;
        border-collapse: collapse;
        border-radius  : 6px;
        margin-bottom  : 2rem;
    }


    &__table .price__row:first-child {
        color           : #7e7e82;
        background-color: #f8f8f8;
    }

    &__row {
        background-color: #fff;
        position        : relative;
        font-size       : 0.875rem;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            &:nth-child(2)::after {
                width: 94%;
                left : 20px;
                top  : 84px;
            }

            &:nth-child(3)::after,
            &:nth-child(4)::after,
            &:nth-child(5)::after {
                width: 94%;
                left : 20px;
                top  : 103px;
            }
        }

        &:nth-child(2)::after,
        &:nth-child(3)::after,
        &:nth-child(4)::after,
        &:nth-child(5)::after {
            content         : "";
            width           : 95%;
            height          : 1px;
            position        : absolute;
            left            : 23px;
            top             : 78px;
            z-index         : 2;
            background-color: #eaeaea;
        }
    }

    &__row td {
        padding: 23px;

        &:first-child {
            width: 50%;
        }

        &:last-child {
            text-align: right;
        }

    }

    &__button {
        width           : 119px;
        height          : 32px;
        border-radius   : 16px;
        background-color: #ff3e79;

        &:hover {
            opacity                   : 0.8;
            transition-delay          : 150ms;
            transition-duration       : 0.5s;
            transition-timing-function: ease-out;
        }

        &::after {
            content    : url(../img/dropdown.svg);
            margin-left: 1rem;
        }

        &:hover::after {
            margin-left               : 0.5rem;
            transition-delay          : 150ms;
            transition-duration       : 0.5s;
            transition-timing-function: ease-out;
        }
    }

    &__button span {
        text-transform: uppercase;
        font-family   : "TT Lakes Medium";
        font-size     : 0.75rem;
        color         : #ffffff;
        text-align    : right;
    }

    &__swiper-container {

        &::after {
            @media screen and (max-width: 320px) {
                content         : "";
                position        : absolute;
                right           : 0;
                top             : 20px;
                width           : 50px;
                height          : 200px;
                background-color: #f8f8f8;
                filter          : blur(8px);
                z-index         : 2;
                transform       : translateX(25px);
            }
        }


    }
}

.endnote {
    &__text {
        margin-bottom: 1rem;
        max-width    : 421px;
        font-size    : 0.875rem;
    }

    &__button {
        text-align: left;
    }

    &__button span {
        margin-right: 10px;
        font-size   : 1rem;
    }

    &__button::after {
        content: url(../img/endnote.svg);
    }

    &__button:hover {
        opacity                   : 0.8;
        transform                 : scale(1.02);
        transition-delay          : 150ms;
        transition-timing-function: ease-in-out;
    }

    &__button:hover::after {
        margin-left               : 0.5rem;
        transition-delay          : 150ms;
        transition-duration       : 0.5s;
        transition-timing-function: ease-out;
    }

}

.footer {
    &--flex {
        display        : flex;
        justify-content: space-between;
        flex-direction : row;
        padding        : 50px 0 25px 0;

        @media screen and (max-width: 320px) {
            flex-direction: column;
            padding-top   : 0;
        }
    }

    &__text {
        width    : 25%;
        font-size: 0.875rem;
        color    : #7e7e82;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            width: 30%;
        }

        &:first-child {
            @media screen and (max-width: 320px) {
                margin-bottom: 1rem;
                width        : 100%;
                text-align   : center;
            }
        }

        &:nth-child(2) {
            text-align: center;

            @media screen and (max-width: 320px) {
                margin-bottom: 1rem;
                width        : 100%;
                text-align   : center;
            }
        }

        &:nth-child(3) {
            text-align: end;

            @media screen and (max-width: 320px) {
                width     : 100%;
                text-align: center;
            }
        }
    }


}